import CountryFlags from "../pictures/flags.jpg"
import Language from "./Language";

const LanguageSkills = () => {
    return <>
        <div className={"container text-center"}>
            <h2 className={"text-decoration-underline pb-3"}>Nyelvi kszsgeim</h2>
            <div className={"row align-item-start"}>
                <div className={"col-6"}>
                    <img src={CountryFlags} alt={"County Flags"}/>
                </div>
                <div className={"col-6"}>
                    <Language languageName={"Magyar"} languageSkill={"Anyanyelv"}/>
                    <Language languageName={"Nmet"} languageSkill={"Trsalgsi szint / B2 tpus szbeli s rsbeli nyelvvizsga"}/>
                    <Language languageName={"Angol"} languageSkill={"Trsalgsi szint"}/>
                    <Language languageName={"Koreai"} languageSkill={"Koreai nyelvi kurzus, kpests azonostja: T/2023/000268-1. Jelenleg is tanulom, online kurzus formjban."}/>
                    <Language languageName={"Knai"} languageSkill={"2024. oktber ta online nyelvi kurzuson veszek rszt, hogy elsajttsam a knai nyelv alapjait"}/>
                </div>
            </div>
        </div>
    </>
}

export default LanguageSkills